.card {
    width: 150px;
    height: 220px;
    border: 1px solid black;
    grid-row: span 6;
    border: 1px solid rgba(0, 0, 205);
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    }
    .card__box {
      margin-bottom: 5px;
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    .card__img {
      width: 140px;
      height: 90px;
      margin-top: 5px;
    }
    
    .card__healthLine {
     
      height: 30px;
      background-color: green;
      text-align: center;
    }
    
    .card__name {
      font-family: 'Montserrat', Arial, sans-serif;
      font-weight: normal;
      font-size: 20px;
      color: rgba(255, 228, 225);
      margin: 0;
      padding: 0;
      max-width: 140px;
      text-align: center;
    }
    
    .card__btn {
      background-color: rgba(0, 0, 0, 0.3);
      border: 1px solid rgba(0, 0, 205);
      border-radius: 15px;
      font-weight: 'Montserrat', Arial, sans-serif;
      font-size: 20px;
      line-height: 1.1;
      color: rgba(255, 228, 225);
      font-weight: normal;
      margin: 3px 0;
      padding: 5px;
      width: 120px;
      height: 35px;
      text-align: center;
    }