.selection {
    width: 100%;
    display: flex;
    justify-content: center;
    background-color: black;
    height: calc(100vh - 122px);
    
  }
  
  .selection__mainContainer {
    position: relative;
    width: 1280px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
  }
  
  .selection__container {
    width: 700px;
    height: 150px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .selection__image {
    position: absolute;
    z-index: 1;
    width: 100%;
    height: 100%;
    object-fit:cover;
  }
  
  .selection__imageStars {
    position: absolute;
    z-index: 1;
    width: 890px;
    
    top: 20px;
    object-fit:cover;
  }
  
  .section__title {
    font-family: 'Averia Serif Libre', Arial, sans-serif;
    font-size: 70px;
    color: rgba(255, 241, 163, 1);
    max-width: 900px;
    margin: 50px 0 0 0;
    padding: 0;
    line-height: 1.2;
    z-index: 100;
  }
  
  .section__subtitle {
    margin: 0;
    padding: 0;
    line-height: 1.2;
    font-weight: 'Montserrat', Arial, sans-serif;
    font-weight: 300;
    color: rgba(255, 241, 163, 1);
    max-width: 900px;
    font-size: 20px;
    z-index: 100;
  }
  
  .section__box {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 50px;
  }
  
  .selection__button {
    background-color: rgba(0, 0, 0, 0.3);
    border: 1px solid rgba(255, 241, 163, 1);
    border-radius: 15px;
    font-weight: 'Montserrat', Arial, sans-serif;
    font-size: 32px;
    line-height: 1.1;
    color: rgba(255, 241, 163, 1);
    font-weight: normal;
    margin: 0;
    padding: 20px;
    width: 250px;
    height: 75px;
    text-align: center;
    z-index: 100;
  }