.auto {
    max-width: 100%;
    height: calc(100vh - 122px);
    background-image: url('../../assets/images/stars.png');
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .auto__box {
    max-width: 700px;
    height: 400px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto 0 auto;
    border: 1px solid rgba(255, 241, 163, 1);
    border-radius: 15px;
    padding: 0 20px 0 20px;
  }
  
  
  .auto__container{
      width: 300px;
      height: 200px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;
  }
  .auto__button {
    background-color: black;
    border: 1px solid rgba(255, 241, 163, 1);
    border-radius: 15px;
    font-weight: 'Montserrat', Arial, sans-serif;
    font-size: 20px;
    line-height: 1.1;
    color: rgba(255, 241, 163, 1);
    font-weight: normal;
    margin: 0 30px;
    padding: 5px;
    width: 150px;
    height: 50px;
    text-align: center;
  }
  
  .disable {
    border: 1px solid rgba(255, 241, 163, 0.5);
    color: rgba(255, 241, 163, 0.5);
  }